import React, { useState } from 'react';
import '../css/BlogPage.css'; // Make sure to create this CSS file for styles

const blogPosts = [
  {
    id: 1,
    title: 'The Impact of Community Service',
    excerpt: 'Community service is vital for personal growth and social improvement. It creates a sense of community and responsibility among individuals...',
    content: "'Sustainable living is a lifestyle choice that focuses on reducing one's environmental impact. It encompasses various practices aimed at preserving resources and protecting the planet. In this blog, we will discuss simple steps that anyone can take to live more sustainably.<br/> Start by reducing waste. Small changes, like using reusable bags, bottles, and containers, can significantly cut down on single-use plastics. Composting organic waste is another excellent way to minimize landfill contributions.<br/> Energy conservation is also crucial. Turn off lights when not in use, opt for energy-efficient appliances, and consider switching to renewable energy sources where possible. These actions not only help the environment but also save money in the long run.<br/> Lastly, support local businesses and sustainable brands. Purchasing locally produced goods reduces transportation emissions and supports your community's economy. Making informed choices contributes to a healthier planet.'",
    author: 'John Doe',
    date: '2024-09-21'
  },
  {
    id: 2,
    title: 'Innovative Solutions for Environmental Issues',
    excerpt: 'In today’s world, innovation is crucial for addressing environmental challenges. Discover how technology is reshaping our approach...',
    content: 'A comprehensive look into innovative technologies, policies, and practices that are making a difference in environmental sustainability...',
    author: 'Jane Smith',
    date: '2024-09-20'
  },
  // Add more blog posts as needed
];

const BlogPage = () => {
  const [selectedPost, setSelectedPost] = useState(null);

  const handleReadMore = (post) => {
    setSelectedPost(post);
  };

  const handleGoBack = () => {
    setSelectedPost(null);
  };

  return (
    <div className="blog-page container mt-5">
      <h2>Our Blog</h2>
      <div className="blog-posts">
        {!selectedPost ? (
          blogPosts.map(post => (
            <div className="blog-card" key={post.id}>
              <h3>{post.title}</h3>
              <p>{post.excerpt}</p>
              <button className="btn btn-read-more" onClick={() => handleReadMore(post)}>Read More</button>
            </div>
          ))
        ) : (
          <div className="blog-detail">
            <h3>{selectedPost.title}</h3>
            <p><em>By {selectedPost.author} on {selectedPost.date}</em></p>
            <p>{selectedPost.content}</p>
            <button className="btn btn-go-back" onClick={handleGoBack}>Go Back</button>
          </div>
        )}
      </div>
    </div>
  );
};

export default BlogPage;
